body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: Spartan, sans-serif;
  font-size: 12px;
  line-height: 22px;
  font-weight: 500;
}
.navbar{
  display: flex;
  justify-content: space-around;
  align-items: baseline;
}


.container-1{
  display: flex;
  margin-left: 85px;
margin-top: 20px;
white-space: nowrap;
}
.box-1{
flex:2;
 justify-content:flex-start;
}
.box-2{
flex:1;

}
.box-2 img{
  padding-right: 50px;
position: relative;
left: 100px;

}
.colorblack{
margin-left: 2%;
  color:#121212;
  text-decoration:none;
 
}
.CleverShop  {
 
  flex: 1;
  font-family: Playfair Display SC;
  font-style: normal;
  font-weight: normal;
  font-size: 24px!important;
